<template>
  <!--   -->
  <div class="container-fluid">
    <b-button-group class="m-1">
      <b-btn
        v-if="playStatus && playStatus.playing"
        @click="pause"
        :disabled="rhythmMode"
        aria-label=" "
      >
        <i class="fa fa-pause"></i>
      </b-btn>
      <b-btn v-else @click="resume" :disabled="rhythmMode" aria-label="">
        <i class="fa fa-play"></i>
      </b-btn>
      <b-btn @click="stop" :disabled="rhythmMode || !playStatus" aria-label=" ">
        <i class="fa fa-stop"></i>
      </b-btn>
    </b-button-group>

    <b-button-group class="m-1 float-right">
      <b-btn @click="addchapter" :disabled="rhythmMode">  </b-btn>

      <b-dropdown no-caret right aria-label="   ">
        <template slot="button-content">
          <i class="fa fa-ellipsis-v"></i>
        </template>

        <b-dropdown-item-button v-show="false"></b-dropdown-item-button>
        <b-dropdown-item-button v-show="false"></b-dropdown-item-button>
        <b-dropdown-item-button v-b-modal.setting> </b-dropdown-item-button>
      </b-dropdown>
    </b-button-group>
  </div>
</template>

<script>
export default {
  props: ['rhythmMode', 'playStatus'],
  methods: {
    addchapter() {
      this.$emit('addchapter')
    },
    resume() {
      this.$emit('play', 'resume')
    },
    pause() {
      this.$emit('play', 'pause')
    },
    stop() {
      this.$emit('play', 'stop')
    }
  }
}
</script>



